@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
html {
  font-size: 100%;
  overflow-x: hidden;
}

body {
  background-color: #252525;
  min-height: 250vh;
  min-width: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

::-webkit-scrollbar {
  width: 7px;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: #202020;
}

::-webkit-scrollbar-thumb {
  background: #6c63ff;
  border-radius: 10px;
  width: 4px;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

header {
  height: 75px;
  width: 100%;
  display: flex;
  flex-direction: row;
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: #202020;
  box-shadow: 0px 0px 0px 3px rgba(108, 99, 255, 0.5);
}

.logo {
  height: 75px;
  width: 200px;
  padding-left: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.line {
  width: 75px;
  height: 3px;
  border-radius: 5px;
  background-color: #6c63ff;
}

.logo-word {
  font-family: "Bebas Neue", cursive;
  font-size: "50px";
  color: white;
  margin: 0;
  margin-top: 2px;
}

.search-box {
  position: absolute;
  background: #303030;
  height: 35px;
  border-radius: 0px;
  border: 2px solid #6c63ff;
  padding: 10px;
  display: flex;
  flex-direction: row;
  margin-left: 200px;
  top: 9px;
}

.search-btn:hover {
  color: white;
}

.search-btn {
  color: #6c63ff;
  float: right;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #303030;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-txt {
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: white;
  font-size: 16px;
  transition: 0.5s linear;
  line-height: 40px;
  width: 300px;
}

.header-right {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-left: 750px;
  width: 100%;
  background-color: #202020;
  border-radius: 20px;
}

.nav {
  display: flex;
  align-items: flex-start;
  width: 200px;
  height: 50px;
  margin-right: 0px;
  position: relative;
  bottom: 2px;
}

.home {
  width: 100px;
  height: 50px;
  border: 2px solid #6c63ff;
  border-radius: 0px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.home-text:hover {
  color: #6c63ff;
}

.code {
  width: 100px;
  height: 50px;
  border: 2px solid #6c63ff;
  margin-right: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.code-text:hover {
  color: #6c63ff;
}

.sign-in {
  width: 250px;
  height: 50px;
  border: 2px solid #6c63ff;
  margin-left: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

a {
  font-family: "Bebas Neue", cursive;
  font-size: 30px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

